import React from "react";
import { useContext, useRef, useEffect } from "react";
import { CrudContext } from "../../context/CrudContext";
import { PlanillaContext } from "../../context/PlanillaContext";
import { Input, Button, Radio } from "antd";
import { notificacion } from "../../helpers/mensajes";
import "./style/buscador.css";
import HojaTareoIndividual from "../../helpers/HojaTareoIndividual";
import {
  handleDownloadExcel,
  handleDownloadExcel1,
} from "../../helpers/tablaExcel";
import { PDFDownloadLink } from "@react-pdf/renderer";

import Item from "antd/es/list/Item";
import { useState } from "react";
const { Search } = Input;

const Buscador = ({
  abrirModal,
  registrar,
  crear,
  exportar,
  cargar,
  actualizarTabla,
  actualizarAsistencia,
  actualizar,
  buscador,
  cargando,
  setCargando,
  data1,
  data2,
  validacion,
  modal,
  data,
}) => {
  const { createData, setFilterText } = useContext(CrudContext);
  const { setControlAsistencia } = useContext(PlanillaContext);
  const [showDownloadLink, setShowDownloadLink] = useState(false);
  const [result2, setResult2] = useState(null);
  const inputFileRef = useRef(null);

  const handleModal = () => {
    abrirModal(true);
  };
  const handleValidacion = () => {
    modal(true);
  };

  const changeHandler = () => {
    inputFileRef.current.click();
  };

  const excelFile = async (e) => {
    let formData = new FormData();
    formData.append("myFile", e.target.files[0]);
    const excel = await fetch(
      `${process.env.REACT_APP_BASE}/asistencia/excel`,
      {
        method: "post",
        body: formData,
      }
    );
    const response = await excel.json();
    if (response) {
      notificacion(response.status, response.msg);
      actualizarTabla();
      setControlAsistencia(false);
      inputFileRef.current.value = null;
    }

    inputFileRef.current.value = null;
  };

  const excelFileDownload = (e) => {
    const result = data1.trabajador_asistencia?.map((dat) => {
      return {
        [dat?.asistencium?.fecha]:
          dat.asistencia === "Permiso"
            ? "P"
            : dat.asistencia === "Asistio"
            ? "X"
            : dat.asistencia === "Falto"
            ? "F"
            : dat.asistencia === "Dia libre"
            ? "DL"
            : dat.asistencia === "Comision"
            ? "C"
            : "F",
      };
    });

    const newObj = Object.assign({}, ...result);
    const result2 = data1?.trabajador_asistencia?.slice(0, 1).map((dat, i) => {
      return {
        Nro: i + 1,
        Nombre: data1?.nombre,
        Dni: data1?.dni,
        Celular: data1?.telefono,
        Cargo: data1?.contratos?.puesto,
        Área: data1?.contratos?.area,
        ...newObj,
      };
    });

    handleDownloadExcel1(result2, "Hoja de tareo", "prueba");
    // setShowDownloadLink(true);
    // setResult2(result2);
  };
  console.log("====================================");
  console.log(data1);
  console.log("====================================");
  return (
    <div
      className="buscador-container"
      style={{
        display: "flex",
        alignItems: "center",
      }}
    >
      <input
        type="file"
        ref={inputFileRef}
        onChange={excelFile}
        style={{ display: "none" }}
      />
      {buscador ? (
        <Search
          placeholder="Ingresa una nombre..."
          onChange={(e) => setFilterText(e.target.value)}
          style={{
            width: 300,
          }}
        />
      ) : (
        <div>
          {registrar !== false ? (
            <Button onClick={handleModal}>Registrar</Button>
          ) : (
            ""
          )}
          {exportar !== false &&
          data1?.firma_gerente &&
          data1?.firma_jefe &&
          data1?.foto ? (
            <Button onClick={excelFileDownload}>Descargar</Button>
          ) : (
            ""
          )}
          {/* {exportar !== false && result2 && (
            <PDFDownloadLink
              document={<HojaTareoIndividual data={result2} />}
              fileName="Hoja de tareo.pdf"
            >
              <Bi>Descargar </Bi>
            </PDFDownloadLink>
          )} */}
          {validacion !== false ? (
            <Button onClick={handleValidacion}>Validación </Button>
          ) : (
            ""
          )}
          {cargar !== false ? (
            <Button style={{ width: "100px" }} onClick={changeHandler}>
              Cargar
            </Button>
          ) : (
            ""
          )}
        </div>
      )}
    </div>
  );
};

export default Buscador;
