import { utils, writeFileXLSX, writeFile } from "xlsx";
export const handleDownloadExcel = (dataSource, sheetName, fileName) => {
  const wb = utils.book_new();
  const ws = utils.json_to_sheet(dataSource);
  utils.book_append_sheet(wb, ws, sheetName);
  writeFileXLSX(wb, `${fileName}.xlsx`);
};

export const handleDownloadExcel1 = (dataSource, sheetName, fileName) => {
  const wb = utils.book_new();
  const headers = [
    "Hoja de Tareo",
    "Header 2",
    "Header 3",
    "Header 4",
    "Header 5",
    "Header 6",
  ];

  // Crear un array con la cabecera y las llaves de los objetos
  const keys = [headers, Object.keys(dataSource[0])];

  // Crear un array con la cabecera, las llaves y los valores de los objetos
  const data = [...keys, ...dataSource.map((obj) => Object.values(obj))];

  // Transformar el array de arrays en un array de objetos
  const rows = data.map((arr) =>
    arr.reduce((acc, val, i) => {
      acc[headers[i]] = val;
      return acc;
    }, {})
  );

  const ws = utils.json_to_sheet(rows);

  // Establecer el ancho de las columnas de la cabecera
  ws["!cols"] = headers.map(() => ({
    width: 20,
    height: 40,
    textAlign: "center",
  }));

  // Establecer el estilo de la cabecera
  ws["!merges"] = [{ s: { r: 0, c: 0 }, e: { r: 0, c: 1 } }];
  ws["A1"].s = { font: { bold: true } };
  ws["B1"].s = { font: { bold: true } };
  ws["C1"].s = { font: { bold: true } };
  ws["D1"].s = { font: { bold: true } };
  ws["E1"].s = { font: { bold: true } };
  ws["F1"].s = { font: { bold: true } };

  utils.book_append_sheet(wb, ws, sheetName);
  writeFileXLSX(wb, `${fileName}.xlsx`);
};
